import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';



function Square(props){
  return (
    <button className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}

class Board extends React.Component {

	renderSquare(i) {
	    return (
	      <Square 
	        value={this.props.squares[i]}
	        onClick={() => this.props.onClick(i)}
	      />
	    );
	  }


  render() {

    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}

class Game extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      history: [{
        squares: Array(9).fill(null),
      }],
      xIsNext: true,
      stepNumber: 0,
      moveOrder: Array(9).fill(null),
    };
  }

  jumpTo(step){

  	this.setState({
  		stepNumber: step,
  		xIsNext: (step % 2) === 0,
  	})
  }


  handleClick(i){
  	const history = this.state.history.slice(0, this.state.stepNumber + 1);
  	const current = history[history.length - 1];
  	const squares = current.squares.slice();
  	const move = moveLocation(i);

  	let moveOrder = this.state.moveOrder.slice();
  	moveOrder[this.state.stepNumber] = move;

    if(calculateWinner(squares) || squares[i]){
      return;
    }

    squares[i] = this.state.xIsNext ? 'X' : 'O';

    this.setState({

      history: history.concat([{
      	squares: squares
      }]),

      xIsNext: !this.state.xIsNext,
      stepNumber: history.length,
      moveOrder: moveOrder,
    });

  }


  render() {

    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);

    const start = <button onClick={() => this.jumpTo(0)}>Go to game start</button>;

    const moves = history.map((step, move) => {
    	if(!move)
    		return;

    	const desc = 'Go to move #' + move + ' (' + this.state.moveOrder[move-1] + ')';

    	return (
    		<li key={move}>
    			<button onClick={() => this.jumpTo(move)}>{desc}</button>
    		</li>
    	);
    });

    let status;

    if(winner){
      	status = 'Winner: ' + winner + '!';
    }
    else if(this.state.stepNumber == 9){
    	status = 'Draw!';
    }
    else{
    	status = 'Next player: ' + (this.state.xIsNext ? 'X' : 'O');
    }

    return (
      <div className="game">
        <div className="game-board">
          {status}
          <Board 
            squares={current.squares}
            onClick={(i) => this.handleClick(i)}
          />
        </div>
        <div className="game-info">
          <div>{start}</div>
          <div className="player-info">
	          <div>
	  			Player: X
	          	<ol>{moves}</ol>
	          </div>

	          <div>
	          	Player: O
	          	<ol>{moves}</ol>
	          </div>
	      </div>

        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

function moveLocation(i){
	let locations = [
		[1,1], 
		[2,1], 
		[3,1], 
		[1,2], 
		[2,2], 
		[3,2], 
		[1,3], 
		[2,3], 
		[3,3],
	];

	return locations[i];
}


